import React from "react"
import { Link, graphql } from "gatsby"
import { getSrc } from "gatsby-plugin-image"
import parse from "html-react-parser"
import { Box, Text } from "@chakra-ui/react"

import {
  PostArticle,
  FeatImage,
  PostTitle,
  ArticleBody,
  EventMeta,
} from "../../styles/postStyles"

import { postOptions } from "../../parseOptions"

// We're using Gutenberg so we need the block styles
import "@wordpress/block-library/build-style/style.css"
import "@wordpress/block-library/build-style/theme.css"

import Layout from "../../components/layout"
import Seo from "../../components/Seo"

const EventTemplate = ({ data: { previous, next, event, customEvent } }) => {
  const featuredImage = {
    fluid:
      event.featuredImage?.node?.localFile?.childImageSharp?.gatsbyImageData,
    fixed:
      event.featuredImage?.node?.localFile?.childImageSharp?.gatsbyImageData
        ?.src,
    alt: event.featuredImage?.node?.alt || ``,
  }

  const imgSrc = getSrc(event.featuredImage)

  const metaDate = new Date(event.metaDate).toISOString()
  const modifiedDate = new Date(event.modified).toISOString()

  const breadcrumbMeta = event.seo.breadcrumbs.map((crumb, index) => {
    const position = index + 1
    const item = {
      "@type": "ListItem",
      position: position,
      item: {
        "@type": "WebPage",
        "@id": `https://www.idrivesocal.com${crumb.url}`,
        url: `https://www.idrivesocal.com${crumb.url}`,
        name: crumb.text,
      },
    }
    return item
  })
  const schema = {
    "@context": "https://schema.org",
    "@graph": [
      {
        "@type": "Organization",
        "@id": "https://www.idrivesocal.com/#organization",
        name: "iDriveSoCal",
        url: "https://www.idrivesocal.com/",
        sameAs: [
          "https://www.facebook.com/idrivesocal/",
          "https://www.instagram.com/idrivesocal/",
          "https://www.linkedin.com/company/idrivesocal/",
          "https://www.youtube.com/channel/UC0HZusGNEBUl3lO7AdprFdQ",
          "https://www.pinterest.com/iDriveSoCal/",
          "https://twitter.com/idrivesocal",
        ],
        logo: {
          "@type": "ImageObject",
          "@id": "https://www.idrivesocal.com/#logo",
          inLanguage: "en-US",
          url:
            "https://www.idrivesocal.com/wp-content/uploads/2019/05/iDriveSoCal.jpg",
          contentUrl:
            "https://www.idrivesocal.com/wp-content/uploads/2019/05/iDriveSoCal.jpg",
          width: 824,
          height: 606,
          caption: "iDriveSoCal",
        },
        image: { "@id": "https://www.idrivesocal.com/#logo" },
      },
      {
        "@type": "WebSite",
        "@id": "https://www.idrivesocal.com/#website",
        url: "https://www.idrivesocal.com/",
        name: "iDriveSoCal",
        description: "Automotive News &amp; Events",
        publisher: { "@id": "https://www.idrivesocal.com/#organization" },
        potentialAction: [
          {
            "@type": "SearchAction",
            target: "https://www.idrivesocal.com/?s={search_term_string}",
            "query-input": "required name=search_term_string",
          },
        ],
        inLanguage: "en-US",
      },
      {
        "@type": "ImageObject",
        "@id": `https://www.idrivesocal.com${event.uri}#primaryimage`,
        inLanguage: "en-US",
        url: imgSrc,
        contentUrl: imgSrc,
        width: 927,
        height: 696,
        caption: featuredImage?.alt,
      },
      {
        "@type": "WebPage",
        "@id": `https://www.idrivesocal.com${event.uri}#webpage`,
        url: `https://www.idrivesocal.com${event.uri}`,
        name: event.title,
        isPartOf: { "@id": "https://www.idrivesocal.com/#website" },
        primaryImageOfPage: {
          "@id": `https://www.idrivesocal.com${event.uri}#primaryimage`,
        },
        datePublished: metaDate,
        dateModified: modifiedDate,
        description: event.seo.metaDesc,
        breadcrumb: {
          "@id": `https://www.idrivesocal.com${event.uri}#breadcrumb`,
        },
        inLanguage: "en-US",
        potentialAction: [
          {
            "@type": "ReadAction",
            target: [`https://www.idrivesocal.com${event.uri}`],
          },
        ],
      },
      {
        "@type": "BreadcrumbList",
        "@id": `https://www.idrivesocal.com${event.uri}#breadcrumb`,
        itemListElement: breadcrumbMeta,
      },
      {
        "@type": ["Article", "NewsArticle"],
        "@id": `https://www.idrivesocal.com${event.uri}#article`,
        isPartOf: {
          "@id": `https://www.idrivesocal.com${event.uri}#webpage`,
        },
        author: {
          "@id":
            "https://www.idrivesocal.com/#/schema/person/3103a769d4bfc520790c66d383460d0c",
        },
        headline: event.title,
        datePublished: metaDate,
        dateModified: modifiedDate,
        mainEntityOfPage: {
          "@id": `https://www.idrivesocal.com${event.uri}#webpage`,
        },
        commentCount: 0,
        publisher: { "@id": "https://www.idrivesocal.com/#organization" },
        image: {
          "@id": `https://www.idrivesocal.com${event.uri}#primaryimage`,
        },
        inLanguage: "en-US",
        potentialAction: [
          {
            "@type": "CommentAction",
            name: "Comment",
            target: [`https://www.idrivesocal.com${event.uri}#respond`],
          },
        ],
        copyrightYear: "2020",
        copyrightHolder: { "@id": "https://www.idrivesocal.com/#organization" },
      },
      {
        "@type": "Person",
        "@id":
          "https://www.idrivesocal.com/#/schema/person/3103a769d4bfc520790c66d383460d0c",
        name: "Tom Smith",
        image: {
          "@type": "ImageObject",
          "@id": "https://www.idrivesocal.com/#personlogo",
          inLanguage: "en-US",
          url:
            "https://secure.gravatar.com/avatar/5aa5f7ce3212b1123ed6e8ae1f486164?s=96&d=mm&r=g",
          contentUrl:
            "https://secure.gravatar.com/avatar/5aa5f7ce3212b1123ed6e8ae1f486164?s=96&d=mm&r=g",
          caption: "Tom Smith",
        },
        description:
          "I grew up in a small town in northern Illinois, graduated from Columbia College Chicago, and produced TV news for network affiliates. The TV news business was fun. But not the hours or many of the stories. And, I shifted to sales. But after many years I started getting fired a lot. Turns out old-school media companies don't like painful honesty and new ideas. Luckily, Al Gore invented the internet. So, I started iDSC. And cover whatever stories I want. ;0)",
        sameAs: ["https://www.linkedin.com/in/tomsmith89/"],
      },
      {
        "@type": "Event",
        name: event.title,
        description: event.seo.metaDesc,
        image: {
          "@id": `https://www.idrivesocal.com${event.uri}#primaryimage`,
        },
        url: `https://www.idrivesocal.com${event.uri}`,
        startDate: event.startDate,
        endDate: event.endDate,
        location: {
          "@type": "Place",
          name: customEvent?.venue?.venue,
          description: "",
          url: customEvent?.venue?.website,
          address: {
            "@type": "PostalAddress",
            streetAddress: customEvent?.venue?.address,
            addressLocality: customEvent?.venue?.city,
            addressRegion: customEvent?.venue?.state,
            postalCode: customEvent?.venue?.zip,
            addressCountry: customEvent?.venue?.country,
          },
          telephone: customEvent?.venue?.phone,
        },
        "@id": `https://www.idrivesocal.com${event.uri}#event`,
        mainEntityOfPage: {
          "@id": `https://www.idrivesocal.com${event.uri}#webpage`,
        },
      },
    ],
  }
  return (
    <Layout previous={previous} next={next}>
      <Seo
        title={event.title}
        description={event.excerpt}
        schemaMarkup={schema}
      />

      <PostArticle
        className="blog-post"
        itemScope
        itemType="http://schema.org/Event"
      >
        <header>
          <PostTitle as="h1" px={{ base: 4, sm: 8, md: 8 }} itemProp="headline">
            {parse(event.title)}
          </PostTitle>
          <EventMeta
            px={{ base: 4, sm: 8, md: 8 }}
            pb="6"
            textAlign="center"
            startDate={event.startDate}
            endDate={event.endDate}
          />

          {/* if we have a featured image for this event let's display it */}
          {featuredImage?.fluid && (
            <FeatImage
              image={featuredImage.fluid}
              alt={featuredImage.alt}
              style={{ marginBottom: 50 }}
            />
          )}
        </header>

        {!!event.content && (
          <ArticleBody p={8} itemProp="articleBody">
            {parse(event.content, postOptions)}
          </ArticleBody>
        )}

        <hr />
      </PostArticle>
      <Box
        mx="auto"
        py="4"
        display="flex"
        flexDirection="row"
        justifyContent="center"
        alignItems="center"
      >
        <Text pr="4" textTransform="uppercase">
          Share:
        </Text>
        <Box
          className="s9-widget-wrapper"
          sx={{ "& > div": { width: "fit-content" } }}
        />
      </Box>
    </Layout>
  )
}

export default EventTemplate

export const pageQuery = graphql`
  query EventById(
    $id: String!
    $slug: String
    $previousPage: String
    $nextPage: String
  ) {
    event: wpTribeEvent(id: { eq: $id }) {
      id
      uri
      slug
      excerpt
      content
      title
      date(formatString: "MMMM DD, YYYY; hh:mm a")
      metaDate: date
      modified
      startDate
      endDate
      seo {
        metaDesc
        focuskw
        breadcrumbs {
          text
          url
        }
      }
      tribeEventsCats {
        nodes {
          id
          name
          uri
        }
      }
      featuredImage {
        node {
          altText
          localFile {
            childImageSharp {
              gatsbyImageData(
                quality: 100
                placeholder: TRACED_SVG
                layout: FULL_WIDTH
              )
            }
          }
        }
      }
    }
    previous: wpTribeEvent(id: { eq: $previousPage }) {
      uri
      title
    }
    next: wpTribeEvent(id: { eq: $nextPage }) {
      uri
      title
    }
    customEvent: tribeEvent(slug: { eq: $slug }) {
      id
      title
      venue {
        venue
        address
        city
        state
        zip
        country
        phone
        website
      }
    }
  }
`
